import { Component, OnInit } from '@angular/core';
import { NgForm, FormGroup, FormControl, Validators } from '@angular/forms';
import { RestDataService } from 'src/app/data-services/rest-data-service';
import { HttpClient } from '@angular/common/http';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  hide: boolean = true;

  signUpForm: FormGroup;

  constructor(private _snackBar: MatSnackBar,private http: RestDataService) { }

  ngOnInit() {

    //Creating Sign Up Form
    this.signUpForm = new FormGroup({
      'name': new FormControl(null, Validators.required),
      'email': new FormControl(null, Validators.email),
      'password': new FormControl(null)
    });

  }

  signUp() {
    let responseMessage: any;
    this.http.saveOneEntity("saveUser", this.signUpForm.value).subscribe((response:any) => {
      responseMessage = response.message;
    }, error => {
      console.log("There is error", error);
    }, () => {
      this._snackBar.open(responseMessage,"Ok",{
        duration: 3000
      })
    });
  }

}
